.list {
  display: flex;
  flex-direction: row;
  column-gap: 1rem;
  list-style-type: none;
  margin-block-start: 0em;
  margin-block-end: 0em;
  padding: 1em 2em 1em 2em;
  justify-content: center;
}

@media (max-width: 768px) {
  .list {
    flex-direction: column;
    row-gap: 2rem;
  }
}
